<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">AddressSelect 地址选择</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                收货地址编辑组件，用于新建、更新收货地址。。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">

            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code>

                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Events</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataEvent"
                        style="width: 100%">
                    <el-table-column
                            prop="eventName"
                            label="事件名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="backParams"
                            label="回调参数">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Address 数据结构</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataAddress"
                        style="width: 100%">
                    <el-table-column
                            prop="key"
                            label="键名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>

<script>
    import AddressEdit from '@/components/template/AddressEdit.vue'

    export default {
        name: 'wladdressedit',
        data() {
            return {
                tableData: [
                    {
                        param: 'addressInfo',
                        desc: '地址对象数据',
                        type: '{}',
                        defaultVal: '',
                    }
                ],
                tableDataEvent: [
                    {
                        eventName: 'save',
                        desc: '点击保存按钮时触发',
                        backParams: 'obj: 地址对象',
                    }
                ],
                tableDataAddress:[
                    {
                        key:'id',
                        desc:'每条地址的唯一标识',
                        type:'number | string'
                    }
                ]
            }
        },
        mounted() {
            this.doCode();
        },
        methods: {
            //一进来需要执行的
            doCode() {
                $('.view-code-btn').click(function () {
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
            //保存回调
            save(d){
                alert('保存');
                console.log(d);
            }

        },
        components: {
            AddressEdit
        }
    }
</script>
<style scoped>

</style>



